<template>
  <div class="recharge">
    <el-dialog title=""
      :visible.sync="recharge"
      width="55%"
      top="0"
      @close="$emit('update:show', false)"
      class="elmSyleNew">
      <div class="lh30 fc000 f18">
        <div class="bc wPer80">
          <div class="fc3279ed"></div>
          <div class="fc3279ed">1=10</div>
          <div class="fc3279ed"></div>
          <div class="fc3279ed">1=1000</div>
          <div class="sub-title">:</div>
          <div class="input400">
            <el-input v-model="rechargeData.account" placeholder=""></el-input>
          </div>
          <div class="sub-title">:</div>
          <div class="input400">
            <el-input v-model="rechargeData.determination" placeholder=""></el-input>
          </div>
          <div class="sub-title">:</div>
          <div>
            <el-select v-model="rechargeData.money" placeholder="">
              <el-option
                v-for="item in rechargeData.options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <div>()</div>
          </div>
          <div class="sub-title">QQ:</div>
          <div class="w400">
            <div class="el-select input300"><el-input v-model="rechargeData.qqNumber" placeholder="QQ"></el-input></div>
            <span>()</span>
            <div>(QQ)</div>
          </div>
          <div class="fcff0000">
            *
          </div>
          <div class="tc mt10">
            <el-button type="primary" @click="openCrab"></el-button>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        recharge: false,         //
        rechargeData:{           //
          account:'',            //
          determination:'',      //
          options: [{            //
            value: '10',
            label: '10'
          }, {
            value: '30',
            label: '30'
          }, {
            value: '50',
            label: '50'
          }, {
            value: '100',
            label: '100'
          }, {
            value: '500',
            label: '500'
          }],
          money:'10',          //
          qqNumber:''          //QQ
        },
      };
    },
    props: {
        show: false
    },
    watch: {
        show () {
            this.recharge = this.show;
        }
    },
    methods: {
      openCrab() {
        const h = this.$createElement;
        this.$msgbox({
          message: '<img src="' + require('../assets/crab.png') + '" class="mt50"><div class="f20 fcwhite"></div>',
          dangerouslyUseHTMLString: true,
          center:true,
          customClass:'construction',
          showCancelButton: false,
        });
      }
    }
  };
</script>




