<template>
  <div class="onlineExplain">
    <el-dialog title=""
      :visible.sync="onlineExplain"
      width="55%"
      top="0"
      @close="$emit('update:show', false)"
      class="elmSyleNew">
      <div class="lh30 fc000 f18">
        <div>
          <div>
            <img src="../assets/onlineThart.png" alt="" class="w">
          </div>
          <div>
              <div class="payBorder">
                <img src="../assets/alipay.png" alt="" class="fl">
                <div class="payTxt fl ml80">
                  <div class="fb mt10"></div>
                  <div class="fc535353 lh24 f16">160</div>
                </div>
                <div class="fl ml100">
                  <span class="immediately bgff9c00 clearfix f26 mt30 cursor" @click="recharge = true"></span>
                </div>
                <div class="cb"></div>
              </div>
              <div class="payBorder mt20">
                <img src="../assets/bankCard.png" alt="" class="fl">
                <div class="payTxt fl ml80">
                  <div class="fb mt10"></div>
                  <div class="fc535353 lh24 f16">55</div>
                </div>
                <div class="fl ml100">
                  <span class="immediately bgff9c00 clearfix f26 mt30 cursor" @click="recharge = true"></span>
                </div>
                <div class="cb"></div>
              </div>
          </div>
          <hr class="hr">
          <div></div>
          <div>1=10</div>
          <div></div>
        </div>
      </div>
    </el-dialog>
    <recharge-dialog :show.sync="recharge"></recharge-dialog>
  </div>
</template>

<script>
  import rechargeDialog from '@/components/rechargeDialog'

  export default {
    data() {
      return {
        recharge: false,         //
        onlineExplain: false,    //
      };
    },
    props: {
        show: false
    },
    watch: {
        show () {
            this.onlineExplain = this.show;
        }
    },
    components: {
        rechargeDialog,              //
    },
    methods: {
      openCrab() {
        const h = this.$createElement;
        this.$msgbox({
          message: '<img src="' + require('../assets/crab.png') + '" class="mt50"><div class="f20 fcwhite"></div>',
          dangerouslyUseHTMLString: true,
          center:true,
          customClass:'construction',
          showCancelButton: false,
        });
      }
    }
  };
</script>




