<template>
  <div class="registerUser">
    <el-dialog title=""
      :visible.sync="registerUser"
      width="500px"
      top="0"
      @close="$emit('update:show', false)"
      class="elmSyleNew">
      <div class="realMain lh35 fc4a4a4a">
        <div class="bc wPer80">
          <div class="input280">
            <div class="w70 tr fl mr5"></div>
            <el-input v-model="register.userName" placeholder=""></el-input>
          </div>
          <div class="input280 mt10">
            <div class="w70 tr fl mr5"></div>
            <el-input v-model="register.nickName" placeholder=""></el-input>
          </div>
          <div class="input280 mt10">
            <div class="w70 tr fl mr5"></div>
            <el-input type="password" v-model="register.passWord" placeholder=""></el-input>
          </div>
          <div class="input280 mt10">
            <div class="w70 tr fl mr5"></div>
            <el-input type="password" v-model="register.passWordConfirm" placeholder=""></el-input>
          </div>
          <div class="input280 mt10">
            <div class="w70 tr fl mr5"></div>
            <el-input v-model="register.realName" placeholder=""></el-input>
          </div>
          <div class="input280 mt10">
            <div class="w70 tr fl mr5"></div>
            <el-input v-model="register.idNumber" placeholder=""></el-input>
          </div>
          <div class="input280 mt10">
            <div class="w70 tr fl mr5"></div>
            <el-input v-model="register.phoneNumber" placeholder=""></el-input>
          </div>
          <div class="tc mt10">
              <el-checkbox v-model="register.userService"></el-checkbox><span @click="userservice = true" class="fc3279ed cursor"></span>
          </div>
        </div>
        <div class="mt20 tc">
          <el-button type="primary" @click="openCrab"></el-button>
          <el-button type="warning" @click="openCrab"></el-button>
        </div>
      </div>
    </el-dialog>
    <userservice-dialog :show.sync="userservice"></userservice-dialog>
  </div>
</template>

<script>
import userserviceDialog from '@/components/userservice'
  export default {
    data() {
      return {
        registerUser: false,     //
        userservice: false,      //
        register: {
          userName:'',        //
          nickName:'',        //
          passWord:'',        //
          passWordConfirm:'', //
          realName:'',        //
          idNumber:'',        //
          phoneNumber:'',     //
          userService:false   //
        },
      };
    },
    props: {
        show: false
    },
    watch: {
        show () {
            this.registerUser = this.show;
        }
    },
    components: {
      userserviceDialog
    },
    methods: {
      openCrab() {
        const h = this.$createElement;
        this.$msgbox({
          message: '<img src="' + require('../assets/crab.png') + '" class="mt50"><div class="f20 fcwhite"></div>',
          dangerouslyUseHTMLString: true,
          center:true,
          customClass:'construction',
          showCancelButton: false,
        });
      }
    }
  };
</script>




