<template>
  <div class="realName">
    <el-dialog title=""
      :visible.sync="realName"
      width="55%"
      top="0"
      @close="$emit('update:show', false)"
      class="elmSyleNew">
      <div class="realMain lh35 fc4a4a4a">
        <div class="realTitle f20 tc fc000">
          
        </div>
        <div class="realSource tc">2018-2-1 12:30 </div>
        <hr class="hr">
        <div class="f18">
          <div> 201821</div>
          <div> 1.</div>
          <div> 2.</div>
          <div> </div>
          <div> </div>
          <div class="tr mt100"></div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        realName: false,         //
      };
    },
    props: {
        show: false
    },
    watch: {
        show () {
            this.realName = this.show;
        }
    },
    methods: {
      openCrab() {
        const h = this.$createElement;
        this.$msgbox({
          message: '<img src="' + require('../assets/crab.png') + '" class="mt50"><div class="f20 fcwhite"></div>',
          dangerouslyUseHTMLString: true,
          center:true,
          customClass:'construction',
          showCancelButton: false,
        });
      }
    }
  };
</script>




