<template>
  <div class="information">
    <el-dialog title=""
      :visible.sync="information"
      width="55%"
      top="0"
      @close="$emit('update:show', false)"
      class="elmSyleNew">
     <div class="lh35 fc000 f16 h300 overflow_y">
        <el-tabs>
          <el-tab-pane label="1.0">
            <div class="ti2em"></div>
            <div class="ti2em"></div>
            <div class="ti2em"></div>
            <div class="ti2em"></div>
            <div class="ti2em">QQ:557849366 </div>
          </el-tab-pane>
          <el-tab-pane label="1.1">
            <div class="ti2em"></div>
            <div class="ti2em">BOSS</div>
            <div class="ti2em"></div>
            <div class="ti2em">QQ:557849366 </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        information: false,     //
      };
    },
    props: {
        show: false
    },
    watch: {
        show () {
            this.information = this.show;
        }
    },
    methods: {
      openCrab() {
        const h = this.$createElement;
        this.$msgbox({
          message: '<img src="' + require('../assets/crab.png') + '" class="mt50"><div class="f20 fcwhite"></div>',
          dangerouslyUseHTMLString: true,
          center:true,
          customClass:'construction',
          showCancelButton: false,
        });
      }
    }
  };
</script>




